html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Space Mono", monospace;
  min-height: 100vh;
  background-color: #c5e4e7;
}

h1,
h2,
h3 {
  margin-top: none;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  max-width: 34.375rem;
  margin: 0 auto;
}
@media (min-width: 40em) {
  .container {
    max-width: 56.25rem;
    padding: 0 1rem;
  }
}

.header {
  text-align: center;
  margin: 4rem auto 2rem;
}
.header__logo {
  object-fit: contain;
}
@media (min-width: 64em) {
  .header {
    margin-bottom: 3rem;
  }
}

.calc__container {
  background-color: white;
  padding: 1rem;
  border-radius: 1rem 1rem 0 0;
}

.form .form-control span {
  display: block;
  position: relative;
}
.form .form-control input {
  text-align: right;
  width: 100%;
}
.form .form-control img {
  position: absolute;
}

.input {
  padding: 0.3125rem;
}
.input__icon {
  width: 1.25rem;
}
.input__input {
  background-color: #26c0ab;
}